import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { TweenMax, Expo, Power3 } from 'gsap'
import { withI18next } from '../../lib/withI18next'
import { AboutContainer } from './style.js'

class About extends React.Component {
  static propTypes = {
    t: PropTypes.any
  }

  static async getInitialProps (props) {
    // const { query, res, store } = props.ctx
    const { store } = props.ctx
    const state = store.getState()

    await new Promise((resolve) => {
      setTimeout(resolve, 700)
    })

    return { state }
  }

  constructor (props) {
    super(props)

    this.delay = 0.4
  }

  progressBarAnimte () {
    this.list = document.querySelectorAll('.progress-bar')
    this.arrList = [].slice.call(this.list)
    this.startParcent = {}
    this.parcent = []

    for (let i = 0; i < this.arrList.length; i++) {
      this.startParcent[i] = { value:0 }
      this.parcent[i] = this.arrList[i].getAttribute('aria-valuenow')
    }

    TweenMax.set(this.arrList, { width:0 })

    TweenMax.staggerTo(this.arrList, 1, {
      cycle: {
        width: (i) => {
          let w = this.arrList[i].getAttribute('aria-valuenow') + '%'
          return w
        }
      },
      ease:Expo.easeOut }, 0.1)

    for (let i = 0; i < this.arrList.length; i++) {
      TweenMax.to(this.startParcent[i], 0.3, {
        value: this.parcent[i],
        onUpdate: () => {
          this.arrList[i].innerHTML = "<span className='font-muli'><small><strong>" + Math.floor(this.startParcent[i].value) + '%</strong></small></span>'
        },
        ease:'linear',
        delay: i * 0.1 })
    }
  }

  componentDidMount () {
    TweenMax.set('h2 strong', { x:-10, autoAlpha:0 })
    TweenMax.to('h2 strong', 1, { x:0, autoAlpha:1, delay:this.delay, ease:Power3.easeOut })
    TweenMax.set('h2 .borderLine, h2 .borderLine span', { width:0 })
    TweenMax.staggerTo('h2 .borderLine, h2 .borderLine span', 1, { width:'100%', delay:this.delay + 0.1, ease:Expo.easeOut }, 0.15)

    TweenMax.set('.fade, .technicalSkills .item', { y:15, autoAlpha:0 })
    TweenMax.staggerTo('.fade', 1, { y:0, autoAlpha:1, delay:this.delay + 0.3, ease:Power3.easeOut }, 0.2)
    TweenMax.staggerTo('.technicalSkills .item', 1, { y:0, autoAlpha:1, delay:this.delay + 0.3, ease:Power3.easeOut }, 0.05, () => {
      // this.progressBarAnimte()
    })
    setTimeout(() => {
      this.progressBarAnimte()
    }, 1100)
  }

  render () {
    const { t } = this.props

    return (
      <AboutContainer>
        <div className='container pt-5 pt-sm-5'>
          <div className='row pb-5'>
            <div className='col-12 col-md-12'>
              <h2 className='pt-3 pt-sm-3 pt-md-0 title font-nixieone'><strong>{t('title')}</strong><span className='borderLine'><span><span /></span></span></h2>
              <p className='intro pt-3 fade' dangerouslySetInnerHTML={{ __html: t('intro') }} />
            </div>
          </div>
          <div className='row pb-5 fade'>
            <div className='technicalSkills col-12' ref={e => { this.skills = e }}>
              <h3 className='mb-2 title font-nixieone'><strong>{t('subTitle_1')}</strong></h3>
              <div className='row'>
                <div className='col-12 col-md-5 item'>
                  <p className='mb-0 label font-muli'><small><strong>{t('skill_1')}</strong></small></p>
                  <div className='progress mb-4'>
                    <div className='progress-bar rounded' role='progressbar' style={{ 'width':'0%' }} aria-valuenow='90' aria-valuemin='0' aria-valuemax='100'><span className='font-muli'><small><strong>0%</strong></small></span></div>
                  </div>
                </div>
                <div className='col-12 col-md-5 offset-md-1 item'>
                  <p className='mb-0 label font-muli'><small><strong>{t('skill_2')}</strong></small></p>
                  <div className='progress mb-4'>
                    <div className='progress-bar rounded' role='progressbar' style={{ 'width':'0%' }} aria-valuenow='90' aria-valuemin='0' aria-valuemax='100'><span className='font-muli'><small><strong>0%</strong></small></span></div>
                  </div>
                </div>
              </div>
              <div className='row'>
                <div className='col-12 col-md-5 item'>
                  <p className='mb-0 label font-muli'><small><strong>{t('skill_3')}</strong></small></p>
                  <div className='progress mb-4'>
                    <div className='progress-bar rounded' role='progressbar' style={{ 'width':'0%' }} aria-valuenow='80' aria-valuemin='0' aria-valuemax='100'><span className='font-muli'><small><strong>0%</strong></small></span></div>
                  </div>
                </div>
                <div className='col-12 col-md-5 offset-md-1 item'>
                  <p className='mb-0 label font-muli'><small><strong>{t('skill_4')}</strong></small></p>
                  <div className='progress mb-4'>
                    <div className='progress-bar rounded' role='progressbar' style={{ 'width':'0%' }} aria-valuenow='70' aria-valuemin='0' aria-valuemax='100'><span className='font-muli'><small><strong>0%</strong></small></span></div>
                  </div>
                </div>
              </div>
              <div className='row'>
                <div className='col-12 col-md-5 item'>
                  <p className='mb-0 label font-muli'><small><strong>{t('skill_5')}</strong></small></p>
                  <div className='progress mb-4'>
                    <div className='progress-bar rounded' role='progressbar' style={{ 'width':'0%' }} aria-valuenow='30' aria-valuemin='0' aria-valuemax='100'><span className='font-muli'><small><strong>0%</strong></small></span></div>
                  </div>
                </div>
                <div className='col-12 col-md-5 offset-md-1 item'>
                  <p className='mb-0 label font-muli'><small><strong>{t('skill_6')}</strong></small></p>
                  <div className='progress mb-4'>
                    <div className='progress-bar rounded' role='progressbar' style={{ 'width':'0%' }} aria-valuenow='60' aria-valuemin='0' aria-valuemax='100'><span className='font-muli'><small><strong>0%</strong></small></span></div>
                  </div>
                </div>
              </div>
              <div className='row'>
                <div className='col-12 col-md-5 item'>
                  <p className='mb-0 label font-muli'><small><strong>{t('skill_7')}</strong></small></p>
                  <div className='progress mb-4'>
                    <div className='progress-bar rounded' role='progressbar' style={{ 'width':'0%' }} aria-valuenow='40' aria-valuemin='0' aria-valuemax='100'><span className='font-muli'><small><strong>0%</strong></small></span></div>
                  </div>
                </div>
                <div className='col-12 col-md-5 offset-md-1 item'>
                  <p className='mb-0 label font-muli'><small><strong>{t('skill_8')}</strong></small></p>
                  <div className='progress mb-4'>
                    <div className='progress-bar rounded' role='progressbar' style={{ 'width':'0%' }} aria-valuenow='10' aria-valuemin='0' aria-valuemax='100'><span className='font-muli'><small><strong>0%</strong></small></span></div>
                  </div>
                </div>
              </div>
              <div className='row'>
                <div className='col-12 col-md-5 item'>
                  <p className='mb-0 label font-muli'><small><strong>{t('skill_9')}</strong></small></p>
                  <div className='progress mb-4'>
                    <div className='progress-bar rounded' role='progressbar' style={{ 'width':'0%' }} aria-valuenow='40' aria-valuemin='0' aria-valuemax='100'><span className='font-muli'><small><strong>0%</strong></small></span></div>
                  </div>
                </div>
                <div className='col-12 col-md-5 offset-md-1 item'>
                  <p className='mb-0 label font-muli'><small><strong>{t('skill_10')}</strong></small></p>
                  <div className='progress mb-4'>
                    <div className='progress-bar rounded' role='progressbar' style={{ 'width':'0%' }} aria-valuenow='50' aria-valuemin='0' aria-valuemax='100'><span className='font-muli'><small><strong>0%</strong></small></span></div>
                  </div>
                </div>
              </div>
              <div className='row'>
                <div className='col-12 col-md-5 item'>
                  <p className='mb-0 label font-muli'><small><strong>{t('skill_11')}</strong></small></p>
                  <div className='progress mb-4'>
                    <div className='progress-bar rounded' role='progressbar' style={{ 'width':'0%' }} aria-valuenow='10' aria-valuemin='0' aria-valuemax='100'><span className='font-muli'><small><strong>0%</strong></small></span></div>
                  </div>
                </div>
                <div className='col-12 col-md-5 offset-md-1 item' />
              </div>
            </div>
          </div>
          <div className='row pb-md-5 fade'>
            <div className='experience pb-md-5 col-12 col-md-6 col-lg-5'>
              <h3 className='mb-2 title font-nixieone'><strong>{t('subTitle_2')}</strong></h3>
              <ul className='list-unstyled'>
                <li className='pb-3 current'>
                  <p className='mb-0' dangerouslySetInnerHTML={{ __html: t('exp_3') }} />
                  <p className='mb-0 font-nixieone'><strong>{t('exp_3_pos')}</strong></p>
                </li>
                <li className='pt-3 pb-3'>
                  <p className='mb-0' dangerouslySetInnerHTML={{ __html: t('exp_2') }} />
                  <p className='mb-0 font-nixieone'><strong>{t('exp_2_pos')}</strong></p>
                </li>
                <li className='pt-3 pb-3'>
                  <p className='mb-0' dangerouslySetInnerHTML={{ __html: t('exp_1') }} />
                  <p className='mb-0 font-nixieone'><strong>{t('exp_1_pos')}</strong></p>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </AboutContainer>
    )
  }
}

export default connect()(withI18next(['about', 'common'])(About))
